
import React, { Suspense, lazy } from 'react'; 
import { Routes, Route, Navigate } from 'react-router-dom';
import { PrivateRoute } from '../Routes/PrivateRoute'; 
import { RestrictedRoute } from '../Routes/RestrictedRoute'; 
import { SpinnerLoader } from './Spinner/Spinner'; 
import ErrorBoundary from '../ErrorBoundary'; 
const LoginPage = lazy(() => import('../pages/LoginPage'));
const RegistrationPage = lazy(() => import('../pages/RegistrationPage'));
const DashboardPage = lazy(() => import('../pages/DashboardPage')); 





export const App = () => {
 

  
  return (
    <ErrorBoundary> {/* Probabil vrei un ErrorBoundary aici */}
      <Suspense fallback={<SpinnerLoader />}> {/* Folosete Suspense pt. lazy loading */}
        <Routes>
          {/* Rut public principal - redirecioneaz spre login sau dashboard */}
          <Route path="/" element={
            
             <PrivateRoute component={<DashboardPage />} redirectTo="/login" />
          } />

          {/* Rute restricionate (nu sunt accesibile dac eti logat) */}
          <Route
            path="/login"
            element={<RestrictedRoute component={<LoginPage />} redirectTo="/" />} // Folosete RestrictedRoute corect
          />
          <Route
            path="/register"
            element={<RestrictedRoute component={<RegistrationPage />} redirectTo="/" />} // Folosete RestrictedRoute corect
          />

          {/* Rute private (accesibile doar dac eti logat) */}
          {/* DashboardPage acioneaz ca layout i va folosi <Outlet> pentru a randa /home, /statistics etc. */}
          <Route
            path="/dashboard" 
            element={<PrivateRoute component={<DashboardPage />} redirectTo="/login" />} 
          >
              {/* Definete aici ce se randeaz N <Outlet /> din DashboardPage */}
              {/* Probabil vrei ca /dashboard s redirecteze spre /dashboard/home */}
              <Route index element={<Navigate to="/dashboard/home" replace />} />
              <Route path="home" element={<div>Home Content Placeholder</div>} /> {/* TODO: nlocuiete cu componenta Home */}
              <Route path="statistics" element={<div>Statistics Content Placeholder</div>} /> {/* TODO: nlocuiete cu componenta Statistics */}
              {/* Adaug alte rute imbricate aici (ex: currency) */}
              {/* <Route path="currency" element={<Currency />} /> */}
          </Route>

          {/* Catch-all pentru rute inexistente */}
          <Route path="*" element={<Navigate to="/" replace />} />

        </Routes>
      </Suspense>
      {/* ToastContainer poate fi pus aici dac foloseti notificri globale */}
      {/* <ToastContainer autoClose={3000} theme="colored" /> */}
    </ErrorBoundary>
  );
};

